import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: () => import('../views/Home.vue')
  },
  {
    path: '/pannellum',
    name: 'pannellum',
    component: () => import('../views/panorama/Pannellum.vue')
  },
  {
    path: '/exampleHome',
    name: 'exampleHome',
    component: () => import('../views/htmledit/ExampleHome.vue')
  }
]

const router = new VueRouter({
  routes
})

export default router
